import React from 'react'
import { Link } from 'react-router-dom'
import { useForm } from '../../hooks/useForm';
import validator from "validator";
import { useDispatch, useSelector } from "react-redux";
import { removeError, setError } from '../../actions/ui';
import { startRegisterWithEmailPassword } from "../../actions/auth";


export const RegisterScreen = () => {
    const dispatch = useDispatch();

    const state = useSelector(state => state);

    const [formValues, handleInputChange] = useForm({
        name: 'Josue',
        email: 'nando@gmail.com',
        password: '12345',
        password2: '12345'
    });

    const {name, email, password, password2} = formValues;

    const handleRegister = (e) => {
        e.preventDefault();
        if(isFormValid()){
            dispatch(startRegisterWithEmailPassword(email, password, name));
        }
    }

    const isFormValid = () => {
        if(name.trim().length === 0){
            dispatch(setError('Name is not valid'))
            return false;
        } else if(!validator.isEmail(email)){
            dispatch(setError('Email is not valid'))
            return false;
        } else if(password !== password2 || password.length < 5){
            dispatch(setError('Password should be at least 6 characters and match each other'))
            return false;
        }
        dispatch(removeError());
        return true;
    }

    return (
        <>
        <h3 className="auth__tittle">Register</h3>
        <form onSubmit={handleRegister} className="animate__animated animate__fadeIn animate_faster">
            {
                state.uiError.msg && 
                ( 
                <div className="auth__alert-error">
                    {state.uiError.msg}
                </div>
                )
            }

        <input
                type="text"
                placeholder="Name"
                name="name"
                className="auth__input"
                autoComplete="off"
                value={name}
                onChange={handleInputChange}
            ></input>

            <input
                type="text"
                placeholder="Email"
                name="email"
                className="auth__input"
                autoComplete="off"
                value={email}
                onChange={handleInputChange}
            ></input>

            <input
                type="password"
                placeholder="Password"
                name="password"
                className="auth__input"
                value={password}
                onChange={handleInputChange}
            ></input>

            <input
                type="password"
                placeholder="Confirm the password"
                name="password2"
                className="auth__input"
                value={password2}
                onChange={handleInputChange}
            ></input>


            <button
            type="submit"
            className="btn btn-primary btn-block mt-5"
            >
                Regiter
            </button>

            <div className="auth__social-network">
            <Link to="/auth/login" className="create__acount link">
                Already register?
            </Link>
            </div>


        </form>
    </>
        )
}
